<template>
    <div>

        <h1>艺术家模块</h1>

        <!-- 主题内容 -->
        <common :loading="loading" :error="error" :datas="artistData"></common>
    </div>
</template>
<script>
    import Common from '../component/common.vue';
    export default {
        data() {
            return {
                artistData: [],
                loading: null,
                error: null,
            }
        },
        created() {
            this.getArtistOplace();
        },
        methods: {

            // 获取图片作品信息
            getArtistOplace() {
                this.error = this.artistData = null;
                this.loading = true;
                this.$axios.get('/api').then(res => { // 假地址
                    this.loading = false;
                    this.artistData = res.data.data;
                }).catch(err => {
                    this.error = err.toString();
                    throw new Error("this.$axios.get: 无法获取数据");
                });
            }
        },
        components: {
            Common
        }
    }
</script>


